/* divider */
.divider > i {
	color: #22A7F0;
	font-size: 21px;
}
.divider {
	position: relative;
	width: 126px;
	margin: 0 auto;
	overflow: hidden;
	text-align: center;
}
.divider:after,
.divider:before {
	content: ' ';
	width: 43px;
	position: absolute;
	border-bottom: dotted 2px #ffffff;
	top: 50%;
	margin-top: -4px;
}
.divider:before {
	left: 0;
}
.divider:after {
	right: 0;
}
.divider.dark:after,
.divider.dark:before {
	border-color: #b2b2b2;
}
